.header {
  background-image: url(../../../build/img/Fondo2.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 90vh;
  width: 100%;
  box-shadow: 0px 3px 7px rgba(0, 0, 0, 0.582);
  animation: animate 18s infinite ease-in-out;
}

@keyframes animate {
  25% {
    background-image: url(../../../build/img/Fondo5.jpg);
    background-size: cover;
  }
  50% {
    background-image: url(../../../build/img/Fondo4.jpg);
    background-size: cover;
  }
  75% {
    background-image: url(../../../build/img/Fondo3.jpg);
    background-size: cover;
  }
  100% {
    background-image: url(../../../build/img/Fondo2.jpg);
    background-size: cover;
  }
}
.navegaciones {
  display: flex;
  justify-content: space-around;
  flex-direction: row;
  transition: all 0.5s;
}
.navegaciones:hover {
  background-color: #FFFFFF;
}
.navegaciones:hover .navegacion a {
  color: #000000;
}

.navegacion {
  display: flex;
  height: 28rem;
  padding: 1rem;
  gap: 2rem;
  flex-direction: column;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    height: 7rem;
    flex-direction: row;
  }
}
.navegacion .enlaces {
  padding: 1rem;
  text-decoration: none;
  color: #FFFFFF;
  height: 3.5rem;
  transition: all 0.3s;
  border-bottom: 1px solid rgba(255, 255, 255, 0);
}
.navegacion .enlaces:hover {
  color: #000000;
  border-bottom: 1px solid #000000;
}

.logs-enlaces {
  text-decoration: none;
  color: #FFFFFF;
  padding: 1.5rem;
  transition: all 0.3s;
  height: 5rem;
}
.logs-enlaces.registro {
  border-radius: 1rem;
  background-color: #F46036;
  padding: 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.5s;
  border-bottom: none;
  box-shadow: 1px 2px 3px #892408;
}
@media (min-width: 1024px) {
  .logs-enlaces.registro {
    padding: 2.5rem;
  }
}
.logs-enlaces.registro:hover {
  border-bottom: none;
  background-color: #f34d1e;
}
.logs-enlaces.inicio {
  color: #f2f2f2;
  border-radius: 1rem;
  background-color: #d9d9d9;
  box-shadow: 1px 2px 3px #b3b3b3;
  display: flex;
  justify-content: center;
  align-items: center;
}
.logs-enlaces.inicio:hover {
  background-color: #cccccc;
}

.contenedor-textos {
  margin-top: 20rem;
}
@media (min-width: 768px) {
  .contenedor-textos {
    margin-top: 50rem;
  }
}
.contenedor-textos h4 {
  margin: 1rem;
  font-size: 2.5rem;
  color: #FFFFFF;
  z-index: 400;
}
.contenedor-textos h1 {
  color: #000000;
  margin: 0rem;
  padding: 0.5rem;
  font-size: 7rem;
  font-weight: bold;
  background-color: #FCF7F8;
  display: inline-block;
  border-radius: 1rem;
  box-shadow: 1px 2px 3px black;
}
.contenedor-textos h1 span {
  color: #F46036;
}
.contenedor-textos p {
  font-size: 3rem;
  margin: 1rem;
  color: #FFFFFF;
}

.bg {
  backdrop-filter: brightness(75%);
  height: 100%;
}

.titulo-servicios {
  margin: 3rem auto;
  display: flex;
  justify-content: center;
}

.servicios {
  width: 95%;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .servicios {
    padding: 2rem;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    place-content: center;
    place-items: center;
    column-gap: 5rem;
    width: 100%;
  }
}
.servicios .servicio {
  margin: 5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
@media (min-width: 768px) {
  .servicios .servicio {
    margin: 0;
  }
}
.servicios .servicio p {
  font-size: 1.5rem;
  margin: 1rem 0;
  text-align: center;
}
.servicios .servicio img {
  width: 10rem;
}
.servicios .servicio h2 {
  color: #F46036;
  margin: 1rem;
  font-size: 2.5rem;
  text-align: center;
}

.vehiculos-titulo {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  text-align: center;
  margin-top: 15rem;
}
.vehiculos-titulo p {
  color: rgba(0, 0, 0, 0.658);
}

.vehiculos {
  display: grid;
  gap: 5rem;
  padding: 2rem;
  place-content: center;
  place-items: center;
}
@media (min-width: 768px) {
  .vehiculos {
    grid-template-columns: 1fr 2fr 1fr;
  }
}
.vehiculos .modelos {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
}
@media (min-width: 768px) {
  .vehiculos .modelos {
    display: flex;
    flex-direction: column;
  }
}
.vehiculos .modelos button {
  transition: all 1s;
  transition: background-color 0.7s;
  border: none;
  margin: 0.5rem 0.5rem;
  padding: 0.5rem;
  color: #000000;
  text-decoration: none;
  background-color: #FCF7F8;
  font-weight: 900;
  letter-spacing: 1px;
  font-size: 2rem;
  border-radius: 1.5rem;
}
@media (min-width: 768px) {
  .vehiculos .modelos button {
    padding: 2rem;
    margin: 0.5rem 0;
  }
}
.vehiculos .modelos button:hover {
  cursor: pointer;
  background-color: #ff3718;
  color: #FFFFFF;
}
.vehiculos .modelos button.activo {
  transition: all 1s;
  background-color: #ff3718;
  color: #FFFFFF;
}

.imagen {
  display: flex;
  justify-content: center;
  align-items: center;
}
@media (min-width: 1024px) {
  .imagen img {
    width: 70%;
  }
}

.caracteristicas {
  width: 90%;
}
@media (min-width: 480px) {
  .caracteristicas {
    width: 85%;
  }
}
@media (min-width: 768px) {
  .caracteristicas {
    width: auto;
  }
}
.caracteristicas .titulo {
  background-color: #ff3718;
  text-align: center;
  color: #FFFFFF;
  padding: 0.2rem 0 0.5rem 0;
}
.caracteristicas .titulo h1 {
  font-weight: 900;
  font-size: 2.5rem;
}
.caracteristicas .listas {
  border-radius: 1rem;
  display: flex;
}
.caracteristicas ul {
  width: 100%;
  margin: 0;
  padding: 0;
  list-style: none;
}
@media (min-width: 768px) {
  .caracteristicas ul {
    width: auto;
  }
}
.caracteristicas ul li {
  width: 100%;
  font-weight: 700;
  border: 1px solid rgba(0, 0, 0, 0.459);
  padding: 3rem;
}
@media (min-width: 768px) {
  .caracteristicas ul li {
    width: auto;
  }
}

.publicidad {
  background-color: #0d0d0d;
  height: 20rem;
  margin: 20rem 0 0 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.publicidad h1 {
  text-align: center;
  color: #FFFFFF;
  font-weight: 900;
  margin: 2rem 0 2rem 0;
}
@media (min-width: 768px) {
  .publicidad h1 {
    font-size: 5rem;
  }
}
.publicidad h1 span {
  color: #F46036;
}
.publicidad p {
  font-size: 2rem;
  text-align: center;
  color: #FFFFFF;
}
@media (min-width: 768px) {
  .publicidad p {
    font-size: 2.5rem;
  }
}

.publicidad-imagen {
  background-image: url(../../../build/img/cochesgris.jpg);
  height: 50rem;
  width: 100%;
  margin-bottom: 5rem;
  box-shadow: 0px 3px 4px rgba(0, 0, 0, 0.582);
}

.publicidad-texto {
  width: 95%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
@media (min-width: 1024px) {
  .publicidad-texto {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    place-content: center;
    place-items: center;
    margin-left: 5rem;
  }
}
.publicidad-texto p {
  margin: 2rem 0 3rem 0;
  font-size: 2rem;
  color: rgba(0, 0, 0, 0.61);
}
.publicidad-texto span {
  color: #F46036;
}
.publicidad-texto a {
  transition: all 1s;
  transition: background-color 0.7s;
  border: none;
  color: #FFFFFF;
  text-decoration: none;
  background-color: #ff3718;
  font-weight: 900;
  letter-spacing: 1px;
  font-size: 2rem;
  border-radius: 1.5rem;
  display: block;
  box-shadow: 1px 2px 5px rgba(0, 0, 0, 0.418);
}
.publicidad-texto a:hover {
  cursor: pointer;
}

.texto {
  width: 95%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-bottom: 5rem;
}
.texto p {
  width: 100%;
  margin: 0 auto;
}
.texto a {
  text-align: center;
  margin-top: 2rem;
  width: 50%;
  padding: 1.5rem;
}
@media (min-width: 768px) {
  .texto a {
    width: 40%;
    padding: 1.5rem;
    margin-top: 2rem;
  }
}

.recos {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 95%;
  margin: 4rem 0;
}
.recos .reco {
  gap: 2rem;
  display: flex;
  justify-content: center;
}
.recos .reco .img {
  display: flex;
  align-items: center;
}
.recos .reco .img img {
  width: 20rem;
}
@media (min-width: 768px) {
  .recos .reco .img img {
    width: 12rem;
  }
}
.recos .reco .img .reco-img {
  width: 25rem;
}
@media (min-width: 768px) {
  .recos .reco .img .reco-img {
    width: 15.2rem;
  }
}
.recos .reco .desc {
  display: flex;
  flex-direction: column;
}
.recos .reco .desc h1 {
  color: #000000;
  margin: 0;
  font-size: 2.5rem;
  font-weight: 900;
}
.recos .reco .desc p {
  margin: 1rem 0 3rem 0;
  font-size: 1.8rem;
  width: 100%;
}
@media (min-width: 768px) {
  .recos .reco .desc p {
    width: 100%;
  }
}

.testimoniales {
  margin: 10rem 0 10rem 0;
}
.testimoniales .titulo {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.testimoniales .titulo h1 {
  text-align: center;
  font-size: 3.5rem;
  margin: 2rem 0;
}
.testimoniales .titulo h2 {
  font-size: 2.5rem;
  margin: 0;
}
.testimoniales .titulo p {
  color: rgba(0, 0, 0, 0.616);
  width: 60%;
  text-align: center;
}
.testimoniales .testimonios {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  gap: 10rem;
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .testimoniales .testimonios {
    flex-direction: row;
    gap: 4rem;
  }
}
@media (min-width: 1024px) {
  .testimoniales .testimonios {
    gap: 10rem;
    margin-top: 7rem;
    flex-direction: row;
  }
}
.testimoniales .testimonios .testimonio {
  width: 40rem;
  display: flex;
  flex-direction: column;
}
.testimoniales .testimonios .testimonio .texto {
  display: flex;
  justify-content: center;
}
.testimoniales .testimonios .testimonio .texto p {
  width: 70%;
  font-size: 2.5rem;
  font-weight: 400;
}
@media (min-width: 768px) {
  .testimoniales .testimonios .testimonio .texto p {
    width: 85%;
  }
}
.testimoniales .testimonios .testimonio .persona {
  display: flex;
  gap: 2rem;
  justify-content: center;
}
@media (min-width: 1024px) {
  .testimoniales .testimonios .testimonio .persona {
    gap: 6rem;
  }
}
.testimoniales .testimonios .testimonio .persona h1 {
  font-size: 2.7rem;
  font-weight: 700;
}
.testimoniales .testimonios .testimonio .persona img {
  width: 4rem;
}
@media (min-width: 1024px) {
  .testimoniales .testimonios .testimonio .persona img {
    width: 5rem;
  }
}

.preguntas-titulo {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.preguntas-titulo h1 {
  margin-top: 15rem;
}
@media (min-width: 768px) {
  .preguntas-titulo h1 {
    margin-top: 5rem;
  }
}
.preguntas-titulo p {
  color: rgba(0, 0, 0, 0.644);
  width: 95%;
  text-align: center;
}
@media (min-width: 768px) {
  .preguntas-titulo p {
    width: auto;
  }
}

.preguntas {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 10rem;
}
@media (min-width: 1024px) {
  .preguntas {
    margin-bottom: 20rem;
  }
}
.preguntas .pregunta {
  width: 90%;
  transition: all 1s;
}
@media (min-width: 768px) {
  .preguntas .pregunta {
    width: 70%;
  }
}
.preguntas .pregunta p {
  color: rgba(0, 0, 0, 0.774);
  text-align: center;
  position: absolute;
  opacity: 0;
  left: -9999px;
  transition: all 1s ease-in-out;
}
.preguntas .pregunta p.mostrar {
  transition: all 0.3s ease-in-out;
  opacity: 1;
  position: static;
  transform: translateX(0);
}
.preguntas .pregunta button {
  width: 100%;
  transition: all 1s;
  transition: background-color 0.7s;
  border: none;
  margin: 0.5rem 0.5rem;
  padding: 2rem;
  color: #000000;
  text-decoration: none;
  background-color: #FCF7F8;
  font-weight: 900;
  letter-spacing: 1px;
  font-size: 2rem;
  border-radius: 1.5rem;
  display: flex;
  justify-content: space-between;
}
.preguntas .pregunta button img {
  filter: brightness(0%);
  width: 3rem;
}
@media (min-width: 768px) {
  .preguntas .pregunta button {
    width: 100%;
  }
}
.preguntas .pregunta button:hover {
  cursor: pointer;
  background-color: #ff3718;
  color: #FFFFFF;
}
.preguntas .pregunta button:hover img {
  filter: brightness(100%);
}
.preguntas .pregunta button.activo {
  background-color: #ff3718;
  color: #FFFFFF;
}
.preguntas .pregunta button.activo img {
  filter: brightness(100%);
}

.footer {
  padding: 1rem;
  box-shadow: 1px 2px 7px #000000;
  display: grid;
  column-gap: 5rem;
  row-gap: 3rem;
}
@media (min-width: 1024px) {
  .footer {
    grid-template-columns: repeat(4, 1fr);
  }
}
.footer .desc {
  color: rgba(0, 0, 0, 0.678);
}
.footer .oldride h1 {
  font-weight: 900;
  letter-spacing: 1px;
}
.footer .oldride h1 span {
  letter-spacing: 1px;
  font-weight: 400;
}
.footer .oldride img {
  width: 5rem;
}
.footer .oldride .mail,
.footer .oldride .telefono {
  display: flex;
}
.footer .contacto {
  display: flex;
  flex-direction: column;
}
.footer .contacto input {
  padding: 2rem;
  border: none;
  text-align: center;
  margin-bottom: 1rem;
}
.footer .contacto a {
  text-align: center;
  width: 100%;
  transition: all 1s;
  transition: background-color 0.7s;
  border: none;
  margin: 0.5rem 0.5rem;
  padding: 2rem;
  color: #FFFFFF;
  text-decoration: none;
  background-color: #ff3718;
  font-weight: 900;
  letter-spacing: 1px;
  font-size: 2rem;
  border-radius: 1.5rem;
}
.footer .contacto a:hover {
  cursor: pointer;
  background-color: #ff3718;
  color: #FFFFFF;
}